import React from 'react';
import {inject, observer} from 'mobx-react';
import * as mobx from 'mobx';
import moment from 'moment';
import DeliveryStore from "@/store/delivery/DeliveryStore";
import {withRouter} from "react-router-dom";
import DeliveryProList from '@/components/delivery/DeliveryProList';
import {Card, List, Input, Checkbox, Button, Icon, Popover, Tabs, Row, Col, Pagination} from 'antd';
import url from 'url';
const { TabPane } = Tabs;
const {Search} = Input;


@inject('DeliveryStore')
@withRouter
@observer
class Delivery extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			projectList:[],
			activeKey:"1",
			searchValue:"",
		}
	}

	componentDidMount() {
		let search = this.props.location.search;
		const key = url.parse(search, true).query.key;
		this.loadPageInfo();
		this.store.isProjectitem = false;
	}

	onSearch=(value)=>{
		// this.store.getProjectPageList(value);
		if(this.state.activeKey==="1"){
			this.getOnGoingProject2(value)
		}else{
			this.getOnGoingProject3(value)
		}
	}

	checkboxOnChange=(e)=>{
		this.store.params2.pageIndex = 1;
		this.store.params3.pageIndex = 1;
		if(this.state.activeKey==="1"){
			if(e.target.checked){
				this.store.params2.userId = localStorage.getItem('userid');
			}else{
				this.store.params2.userId = "";
			}
			this.getOnGoingProject2("");
		}else{
			if(e.target.checked){
				this.store.params3.userId = localStorage.getItem('userid');
			}else{
				this.store.params3.userId = "";
			}
			this.getOnGoingProject3("");
		}
	}

	goToKeyLink=(url)=>{
		this.props.history.push(url);
	}

	loadPageInfo = () =>{
		this.store.params2.userId = "";
		this.store.params3.userId = "";
		this.store.getProjectPageList('');
		this.getOnGoingProject2("");
		this.getOnGoingProject3("")
	}

	handleSearchValue=(e)=>{
		this.setState({searchValue: e.target.value})
	}

	handleCreate = () => {
		this.store.isProjectitem = true;
	}

	searchAndMgr=(
		<div style={{marginRight:'30px'}}>
			<Search
				ref={node => (this.search = node)}
				placeholder="搜索"
				onSearch={value=>{this.onSearch(value)}}
				style={{width: 252}}
			/>
			<div style={{cursor:"pointer"}} onClick={this.handleCreate}  className='ml15 dib'><Icon type="setting" /><span style={{cursor:"pointer"}} className='ml5'>管理项目</span></div>
		</div>
	)


	getOnGoingProject2=(text)=>{
		this.store.getProjectPageList2(text)
	}

	getOnGoingProject3=(text)=>{
		this.store.getProjectPageList3(text)
	}

	goToLink=(url)=>{
		this.props.history.push(url);
	}

	callback=(key)=> {
		this.setState({activeKey: key})
	}

	showSizeChange = async (current, pageSize) => {
		this.store.params2.pageIndex = 1;
		this.store.params2.pageSize = pageSize;
		this.getOnGoingProject2('');
	}

	onPageChange = async (pageNumber) => {
		this.store.params2.pageIndex = pageNumber;
		if(pageNumber>1)this.store.params2.pageSize= 20
		if(pageNumber===1)this.store.params2.pageSize= 19
		this.getOnGoingProject2('');
	}

	showTotal = (total) => {
		return `共 ${total} 条`;
	}

	showSizeChange3 = async (current, pageSize) => {
		this.store.params3.pageIndex = 1;
		this.store.params3.pageSize = pageSize;
		this.getOnGoingProject3('');
	}

	onPageChange3 = async (pageNumber) => {
		this.store.params3.pageIndex = pageNumber;
		if(pageNumber>1)this.store.params3.pageSize= 20
		if(pageNumber===1)this.store.params3.pageSize= 19
		this.getOnGoingProject3('');
	}

	showTotal3 = (total) => {
		return `共 ${total} 条`;
	}

	getProjectPurpose = (str) => {
		const arr = str===null?"": str.substr(1, str.length - 2);
		return arr.split(",").join("、")
	}

	render() {
		const { activeKey } = this.state;
		const { projectList2, params2, projectTotalCount2, projectList3, params3, projectTotalCount3, isProjectitem } = this.store;
		const {pageIndex, pageSize} = mobx.toJS(params2);
		//const operations = <div><Button style={{cursor:'pointer'}} type="link" onClick={this.handleCreate}>项目管理</Button></div>;
		return (
			<div className="w h">
				{isProjectitem==false||isProjectitem==null?
					<Tabs defaultActiveKey="1" activeKey={activeKey} onChange={this.callback} tabBarExtraContent={this.searchAndMgr}>
					<TabPane tab="进行中" key="1">
						<span style={{display:''}}><Checkbox onChange={this.checkboxOnChange}></Checkbox><span style={{marginLeft:'5px'}}>只看我的项目</span></span>
						<div className="img-list-delivery">
							<ul>
								{
									projectList2.map((item,index)=>{
										if(index===0&& pageIndex===1){
											return(
												<div style={{display:'inline'}}>
													<li key={index} className="newProject">
														<div 	onClick={()=>{this.goToLink('/delivery/newproject')}} style={{cursor:'pointer'}}>
															<div className="imgWrap" >
																<div className="imgBox" >
																	<Button type="dashed" className='newButton' >
																		<Icon type="plus" /> 创建项目
																	</Button>
																</div>
															</div>
															<div className="title">&nbsp;&nbsp;</div>
															<div className="title">&nbsp;&nbsp;</div>
														</div>
													</li>
													<li key={index} >
														<div 	onClick={()=>{this.goToKeyLink('/delivery/keylink?projectId='+item.id)}}  style={{cursor:'pointer'}}>
															<div className="imgWrap" >
																<div className="imgBox" >
																	<img  src={item.mainPhoto===null?'https://xinlj.oss-cn-beijing.aliyuncs.com/mainphoto/hrwxf.jpg':item.mainPhoto} />
																</div>
															</div>
															<div className="title">{item.name}</div>
															<div className="title2">项目经理：{item.masterName}</div>
														</div>
														<div onClick={()=>{this.goToKeyLink('/delivery/keylink?projectId='+item.id)}}  className="mask_tips">
															<div className="project_name" title={item.name}>{item.name}</div>
															<div className="project_desc">
																<div className="long_name" title={item.companyName}>甲方单位：{item.companyName==""||item.companyName==null?"未确定":item.companyName}</div>
																<div>项目经理：{item.masterName}</div>
																<div className="long_name" title={item.purpose==null||item.purpose==""?"未确定":item.purpose}>项目用途：{item.purpose==null||item.purpose==""?"未确定":item.purpose}</div>
																<div>开工日期：{item.tempBuildBeginTime==0||item.tempBuildBeginTime==null?"未确定":moment(item.tempBuildBeginTime).format("YYYY-MM-DD")}</div>
															</div>
														</div>
													</li>
												</div>

											)
										}else{
											return(
												<li key={index}>
													<div 	onClick={()=>{this.goToKeyLink('/delivery/keylink?projectId='+item.id)}}  style={{cursor:'pointer'}}>
														<div className="imgWrap" >
															<div className="imgBox" >
																<img  src={item.mainPhoto===null?'https://xinlj.oss-cn-beijing.aliyuncs.com/mainphoto/hrwxf.jpg':item.mainPhoto} />
															</div>
														</div>
														<div className="title">{item.name}</div>
														<div className="title2">项目经理：{item.masterName}</div>

													</div>
													<div onClick={()=>{this.goToKeyLink('/delivery/keylink?projectId='+item.id)}}  className="mask_tips">
														<div className="project_name" title={item.name}>{item.name}</div>
														<div className="project_desc">
															<div className="long_name" title={item.companyName}>甲方单位：{item.companyName==""||item.companyName==null?"未确定":item.companyName}</div>
															<div>项目经理：{item.masterName}</div>
															<div className="long_name" title={item.purpose==null||item.purpose==""?"未确定":item.purpose}>项目用途：{item.purpose==null||item.purpose==""?"未确定":item.purpose}</div>
															<div>开工日期：{item.tempBuildBeginTime==0||item.tempBuildBeginTime==null?"未确定":moment(item.tempBuildBeginTime).format("YYYY-MM-DD")}</div>
														</div>
													</div>
												</li>
											)
										}
										return(
											<li key={index}>
												<div 	onClick={()=>{this.goToKeyLink('/delivery/keylink?projectId='+item.id)}}  style={{cursor:'pointer'}}>
													<div className="imgWrap" >
														<div className="imgBox" >
															<img  src={item.mainPhoto===null?'https://xinlj.oss-cn-beijing.aliyuncs.com/mainphoto/hrwxf.jpg':item.mainPhoto} />
														</div>
													</div>
													<div className="title">{item.name}</div>
												</div>

											</li>
										)
									})
								}
							</ul>
						</div>
						<Pagination
							className="mt20 tr"
							current={pageIndex}
							pageSize={pageSize}
							total={projectTotalCount2}
							onShowSizeChange={this.showSizeChange}
							onChange={this.onPageChange}
							showSizeChanger
							showQuickJumper
							showTotal={this.showTotal}
						/>
					</TabPane>
					<TabPane tab="已完成" key="2">
						<span style={{display:''}}><Checkbox onChange={this.checkboxOnChange}></Checkbox><span style={{marginLeft:'5px'}}>只看我的项目</span></span>
						<div className="img-list-delivery">
							<ul>
								{
									projectList3.map((item,index)=>{
										return(
											<li key={index}>
												<div 	onClick={()=>{this.goToKeyLink('/delivery/keylink?projectId='+item.id)}}  style={{cursor:'pointer'}}>
													<div className="imgWrap" >
														<div className="imgBox" >
															<img  src={item.mainPhoto===null?'https://xinlj.oss-cn-beijing.aliyuncs.com/mainphoto/hrwxf.jpg':item.mainPhoto} />
														</div>
													</div>
													<div className="title">{item.name}</div>
													<div className="title2">项目经理：{item.masterName}</div>
												</div>
												<div onClick={()=>{this.goToKeyLink('/delivery/keylink?projectId='+item.id)}}   className="mask_tips">
													<div className="project_name" title={item.name}>{item.name}</div>
													<div className="project_desc">
														<div className="long_name" title={item.partyUnits}>甲方单位：{item.partyUnits}</div>
														<div>项目经理：{item.masterName}</div>
														<div className="long_name" title={this.getProjectPurpose(item.purpose)}>项目用途：{this.getProjectPurpose(item.purpose)}</div>
														<div>开工日期：{item.tempBuildBeginTime!==0?moment(item.tempBuildBeginTime).format("YYYY-MM-DD"):""}</div>
													</div>
												</div>
											</li>
										)
									})
								}
							</ul>
						</div>
						<Pagination
							className="mt20 tr"
							current={mobx.toJS(params3.pageIndex)}
							pageSize={mobx.toJS(params3.pageSize)}
							total={projectTotalCount3}
							onShowSizeChange={this.showSizeChange3}
							onChange={this.onPageChange3}
							showSizeChanger
							showQuickJumper
							showTotal={this.showTotal3}
						/>
					</TabPane>
				</Tabs>:null}
				{/*管理项目*/}
				{isProjectitem?<DeliveryProList/>:null}
			</div>
		)
	}
}


export default Delivery;



